.product {
    &:hover {
        & img {
            opacity: 0.9;
        }
    }
}

.product-image {
    & img {
        border: 1px solid #eee;
    }
}

.product {
    
    & h2 {
        margin-bottom: 10px;
        text-align: center;
        font-size: 1.8rem;
        padding: 0 20px;
    }

    & p {
        text-align: center;
    }
}

.product-image {
    height: 300px;
    margin-bottom: 10px;
    cursor: pointer;
}

.product-detail {
    width: 950px;
    padding: 20px;

    & h2 {
        font-size: 2.2rem;
        margin-bottom: 10px;
    }
}