.quantity {
    border: 1px solid #eee;
    border-radius: 4px;
    margin-right: 10px;
    padding: 6px 4px;
    min-width: 100px;

    & span {
        padding-inline: 10px;
        text-transform: uppercase;
    }

    & button {
        outline: none;
        background: transparent;
        border: 0;
        width: 24px;
        cursor: pointer;
        font-size: 2rem;
        
        & i {
            margin-bottom: -2px;
        }
    }
}

.detailpage-detail {
    padding-left: 20px;
}